<template>
    <div class="title-field">
        <template v-if="props.type === 'field'">
            <Field
                v-for="item in data.items" :key="item.code"
                :icon="item.icon"
                :placeholder="item.name"
                :value="data.value[item.code]"
                class="noerror light"
                :class="{ ltr: !item.rtl, rtl: item.rtl }"
                @change="onSearchChange($event, item.code)"
            />
        </template>
        <template v-else-if="props.type === 'textarea'">
            <Textarea
                v-for="item in data.items" :key="item.code"
                :icon="item.icon"
                :placeholder="item.name"
                :value="data.value[item.code]"
                class="noerror light with-placeholder"
                :class="{ ltr: !item.rtl, rtl: item.rtl }"
                @change="onSearchChange($event, item.code)"
            />
        </template>
    </div>
</template>

<script setup>
    import { ref, watch, reactive, onMounted } from 'vue';
    import Field from 'ðŸ”—/components/ui/Field.vue';
    import Textarea from 'ðŸ”—/components/ui/Textarea.vue';
    import { LANGUAGES_LIST } from 'ðŸ”—/constants';

    const props = defineProps({
        value: { type: Object, default: () => ({}) },
        type: { type: String, default: 'field' }
    });

    const data = reactive({
        value: { ...props.value },
        items: LANGUAGES_LIST
    });

    const emit = defineEmits(['change']);

    const onSearchChange = (e, code) => {
        data.value[code] = e.target.value

        emit('change', data.value);
    }

    onMounted(() => {
    })
</script>

<style lang="scss" scoped>
    .title-field {
        display: flex;
        flex-direction: column;
        gap: 8px;
        flex: 1;
        align-items: start;

        .textarea,
        .field {
            flex: 1 1 100%;
            width: 100%;
            max-width: var(--input-max-width);
        }
    }
</style>
